@import url(https://fonts.googleapis.com/css?family=Roboto+Mono&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  background-image: url("/my-portfolio/static/media/background (2).306327fe.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  font-family: 'Roboto Mono';
  min-height: 100vh;
  min-width: 320px;
  position: relative;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
}

.App-link {
  color: #61dafb;
}

.filter {
  background-color: rgba(0, 0, 0, 0.5);
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 0;
}
.nav-bar {
    width: 100%;
    height: 65px;
    position: absolute;
    top: 0;
    z-index: 2;
    background-color: rgb(21, 20, 26);
}

.nav-text {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    flex-direction: row;
    color: darkgray;
    padding-right: 10px;
}

#tabs {
    float: right;
    padding: 23px;
}

.tab {
    display: block;
    padding-left: 10px;
    padding-right: 10px;
}

.tab:hover {
    cursor: pointer;
    color: rgb(75, 75, 75);
}

.active {
    color: rgb(75, 75, 75);
    cursor: auto;
    cursor: initial;
}


.name {
    float: left;
    padding: 15px;
    color: darkgray;
    font-size: 20px;
}

@media only screen and (max-width: 550px) {
    .nav-text{
        display: none;
    } 
}
.about {
    text-align: left;
    margin: auto;
    width: 95%;
    color: white;
    padding-top: 80px;
    padding-bottom: 40px;
}

.about-intro {
    margin-bottom: 30px;
}

.about-section-top {
    background-color: rgba(53, 0, 66, 0.8);
    color: slateblue;
    width: 100%;
    padding: 5px;
    padding-left: 10px;
    margin-bottom: 5px;
    text-align: left;
    border-radius: 15px 15px 0 0;
    z-index: 2;
}

.about-section-bottom {
    color: white;
    width: 100%;
    margin-bottom: 23px;
    border-radius: 0 0 15px 15px;
    text-align: left;
    min-height: 151.2px;
    max-height: 100px;
    z-index: 2;
    border: solid black 1px;
}

.skill-section-bottom {
    color: darkgray;
    width: 100%;
    margin-bottom: 23px;
    border-radius: 0 0 15px 15px;
    text-align: left;
    min-height: 155px;
    z-index: 2;
}

#about-bottom {
    background-color: rgb(21, 20, 26, 0.8);
    padding: 10px;
}



.about-skill-wrap {
    margin-left: 2px;

}

.about-icon {
    float: right;
    color:cornflowerblue;
    opacity: 0.1;
    margin-right: 10px;
}

.skill-icon {
    float: left;
    margin-left: 7px;
    margin-right: 15px;
    margin-top: 7px;
}

.about-skill {
    background-color: rgba(30,30,30, .7);
    height: 35px;
    margin-bottom: 3px;
    border: black 1px solid;
}

.about-skill p {
    padding-top: 5px;
}

#last {
    border-radius: 0 0 15px 15px;
    margin-bottom: 0;
}

.about h3 {
    z-index: 2;
    position: relative;
    color: white;
    padding-top: 30px;
    padding-bottom: 30px;
}

.it-me {
    border-radius: 50%;
    border: solid black 1px;
    float: right;
    position: relative;
    top: -5px;
}

.bold {
    color: lightcoral;
    font-style: italic;
}

.top-bold-tech {
    color: cornflowerblue;
    font-style: italic;
}

.top-bold-design {
    color: cornflowerblue;
    font-style: italic;
}

.robot {
    margin: auto;
    display: block;
    margin-left: auto;
    margin-right: auto;
}

@media only screen and (max-width: 780px) {
    .skill-icon {
        display: none;
    }
    .about-icon {
        display: none;
    }
    .about-skill p {
        padding-left: 5px;
    }

    .about-section-bottom {
        font-size: 13px;
        min-height: 136px;
    }
}

@media only screen and (max-width: 500px) {
    .it-me{
        display: none;
    } 

    .skill-icon {
        display: block;
    }

    .about-skill-wrap {
        margin-left: 0;
        position: relative;
        width: 106.3%;
        left: -15px;
    }

    .about-section-bottom {
        min-height: 200px;
    }

    .about-section-bottom {
        font-size: 16px;
        min-height: 180px;
    }
}
.cv-doc {
    overflow: hidden;
}

.cv-page {
    padding: 10px;
    position: relative;
    margin-left: 10px;
    margin-right: 10px;
}

.cv-modal {
    overflow: hidden;
    height: 1050px;
    border-radius: 10px;
}

.cv-modal button {
    margin: 10px;
    background-color: paleturquoise;
}

.close-modal-icon {
    position: absolute;
    top: 5px;
    right: 5px;
    color: darkgray;
}

.close-modal-icon:hover {
    color: gray;
}

.download {
    background-color: teal;
    height: 30px;
    margin: auto;
    display: block;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    margin-left: 20px;
    margin-right: 20px;
    position: relative;
    top: -150px;
}

.download a {
    color: white;
}



@media only screen and (max-width: 700px) {
    .cv-page {
        position: relative;
        left: -50px;
    }
}
.portfolio {
    margin: auto;
    width: 80vw;
    padding: 10px;
    padding-top: 20px;
}

.project-box {
    padding: 5px;
    margin-top: 15px;

    max-width: 1100px;
}

.project-info {
    background-color: rgba(53, 0, 66, 0.8);
    margin-bottom: 5px;
    width: 100%;
    color: slateblue
}

.project-links{
    margin-top: 5px;
    height: 25px;
    width: 100%;
    padding-bottom: 85px;
    padding-top: 4px;
}

.project-links a {
    color: slateblue;
}

#git-link {
    float: left;
    width: 49.5%;
    background-color: rgba(53, 0, 66, 0.8);
    padding: 2px;
}

#git-link:hover {
    background-color: rebeccapurple;
}

#live-link {
    float: right;
    padding: 2px;
    width: 49.5%;
    background-color: rgba(53, 0, 66, 0.8);
}

#live-link:hover {
    background-color: rebeccapurple;
}

.project-image-wrap {
    position: relative;
    width: 100%;
    height: 43vw;
    min-height: 500px;
    max-height: 800px;
    background-color: rgb(21, 20, 26, 0.8);
    margin-left: none;
}

.project-image img {
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left:0;
    padding: 1vw;
    z-index: 1;
}

.project-image-next img {
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left:0;
    padding: 1vw;
    z-index: 2;
    opacity: 0;
}

#fade-in img {
    -webkit-animation-name: fade;
            animation-name: fade;
    -webkit-animation-duration: 3000ms;
            animation-duration: 3000ms;

}

@-webkit-keyframes fade {
    from {opacity:0;}
    to {opacity:1;}
  }

@keyframes fade {
    from {opacity:0;}
    to {opacity:1;}
  }


.hover-tint-inactive {
    width: 81.5%;
    height: 95%;
    display: none;
    position: absolute;
}

.hover-text-inactive {
   display: none;
}

.hover-tint-active {
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: rgba(250, 250, 250, 0.4);
    z-index: 3;
    position: absolute;
    border: double  plum 4px;
}

.hover-text-active {
   opacity: 100%;
   width: 100%;
   position: absolute;
   top: 0;
   margin: auto;
   color: black;
   z-index: 8;
   background-color:  plum;
   padding: 5px;
}

.features-active {
    display: flex;
    flex-direction: column;
    float: left;
    z-index: 4;
    position: absolute;
    bottom: -15px;
    text-align: left;
    background-color: plum;
    width: 100%;
}

.features-inactive {
    display: none;
}

.project-preview-list {
    list-style: none;
    display: flex;
    flex-direction: column;
    margin: 0;
    padding: 0;
    position: relative;
    left: -22.5px;
    width: 5.5vw;

}

#project-num {
    opacity: 0.5;
}

@media only screen and (max-width: 767px) {
    .project-previews {
        display: block;
        margin-left: auto;
        margin-right: auto;
        padding-top: 5px;
        width: 64%;
    }
    
    .project-preview-list {
        list-style: none;
        display: flex;
        flex-direction: row;
    }

    .project-thumbnail {
        margin-right: 1vw;
        margin-left: 1vw;
    }

    .project-tech {
        display: none;
    }

    .arrow {
        -webkit-transform: rotate(-90deg);
                transform: rotate(-90deg);
    }
}

@media only screen and (max-width: 500px) {
    .project-previews {
        width: 85.5%;
    }
}

.project-thumbnail {
    margin-bottom: .5vw;
    margin-top: .5vw;
}

.project-num {
    position: absolute;
    text-align: center;
    color: purple;
    opacity: 40%;
}

.project-techs {
    list-style: none;
    margin: 0;
    display: flex;
    flex-direction: row;
    padding: 7px;
    position: absolute;
    top: -7px;
    right: 15px;
}

.project-tech {
    padding: 2px;
    opacity: 70%;
}

.project-name {
    text-align: left;
    padding: 7px;
}

.arrow {
    color: slateblue;
}

.arrow:hover {
    color: cornflowerblue;
}
.preview img {
    height: 5.4vw;
    width: 5.4vw;
    min-height: 40px;
    min-width: 40px;
}

#current-preview img {
    box-shadow: 0 0 2px 2px plum ;
}

#not-current-preview img:hover {
    box-shadow: 0 0 2px 2px plum ;
}

.contact {
    text-align: center;
    margin: auto;
    width: 50%;
    padding: 10px;
}

.footer {
    color: rgb(75, 75, 75);
    height: 50px;
    width: 100%;
    position: absolute;
    bottom: 0;
    padding: 5px;
    background-color: rgb(21, 20, 26, 0.8);
}

.footer p {
    float: right;
    padding: 5px;
    margin-right: 10px;
}

#social-links {
    float: left;
    margin-left: 10px;
}

.social {
    display: block;
    padding-left: 10px;
    padding: 4px;
}

.social a {
    color: darkgray;
}

.social a:hover {
    color: rgb(75, 75, 75);;
}
